/* 
вес груза / объем = результат < 250коэфф то фиксированая цена до 50кг и объема 0.2 м3 если больше вес груза * на тариф повесу

По объему 
цена за 1м3 * объем
*/

const updateForm = function(inp) {
  let otpravlenie = document.querySelector('#otpravlenie');
  let polycenie = document.querySelector('#polycenie');

  let ves = document.querySelector('#slider-ves').value;
  let objom = document.querySelector('#slider-objom').value;
  let price = document.querySelector('#slider-price');
  let dostavka = document.querySelector('#slider-dostavka');

  if(inp.id === 'otpravlenie') {
    if(vKazan[otpravlenie.value.toLowerCase()] === undefined) {
      price.textContent = '—';
    }

    if(vKazan[otpravlenie.value.toLowerCase()] !== undefined) {

      if(ves > 49 && objom < 0.3) {
        let newVes = 0;
        if(ves >= 49 && ves <= 100) newVes = vKazan[inp.value.toLowerCase()].ves100;
        if(ves >= 100.001 && ves <= 300) newVes = vKazan[inp.value.toLowerCase()].ves300;
        if(ves >= 500.001 && ves <= 1000) newVes = vKazan[inp.value.toLowerCase()].ves500;
        if(ves >= 1000.001 && ves <= 2000) newVes = vKazan[inp.value.toLowerCase()].ves1000;
        if(ves >= 2000.001 && ves <= 3000) newVes = vKazan[inp.value.toLowerCase()].ves2000;
        if(ves >= 3000.001 && ves <= 5000) newVes = vKazan[inp.value.toLowerCase()].ves3000;
        if(ves >= 5000.001 && ves <= 10000) newVes = vKazan[inp.value.toLowerCase()].ves5000;
      
        price.textContent = Math.round(newVes * ves) + ' ₽';
      }

      if(ves < 49 && objom < 0.2) {
        price.textContent = vKazan[inp.value.toLowerCase()].minPrice + ' ₽';
      }

      if(objom > 0.2 && objom < 100) {
        let newObjom = 0;
        if(objom <= 0.6 && objom > 0) newObjom = vKazan[inp.value.toLowerCase()].objom06;
        if(objom >= 0.7 && objom <= 3) newObjom = vKazan[inp.value.toLowerCase()].objom3;
        if(objom >= 3.001 && objom <= 5) newObjom = vKazan[inp.value.toLowerCase()].objom5;
        if(objom >= 5.001 && objom <= 12) newObjom = vKazan[inp.value.toLowerCase()].objom12;
        if(objom >= 12.001 && objom <= 20) newObjom = vKazan[inp.value.toLowerCase()].objom20;
        if(objom >= 20.001 && objom <= 30) newObjom = vKazan[inp.value.toLowerCase()].objom30;
        if(objom >= 30.001 && objom <= 40) newObjom = vKazan[inp.value.toLowerCase()].objom40;
        if(objom >= 40.001 && objom <= 100) newObjom = vKazan[inp.value.toLowerCase()].objom40;

        price.textContent = Math.round(newObjom * objom) + ' ₽';
      }

      dostavka.textContent = vKazan[inp.value.toLowerCase()].dostavkaDay;
      polycenie.value = 'Казань';
    }
  }

  if(inp.id === 'polycenie') {
    if(izKazan[polycenie.value.toLowerCase()] === undefined) {
      price.textContent = '—';
    }

    if(izKazan[polycenie.value.toLowerCase()] !== undefined) {
      price.textContent = izKazan[inp.value.toLowerCase()].minPrice + ' ₽';

      if(ves > 49 && objom < 0.3) {
        let newVes = 0;
        if(ves >= 49 && ves <= 100) newVes = izKazan[inp.value.toLowerCase()].ves100;
        if(ves >= 100.001 && ves <= 300) newVes = izKazan[inp.value.toLowerCase()].ves300;
        if(ves >= 500.001 && ves <= 1000) newVes = izKazan[inp.value.toLowerCase()].ves500;
        if(ves >= 1000.001 && ves <= 2000) newVes = izKazan[inp.value.toLowerCase()].ves1000;
        if(ves >= 2000.001 && ves <= 3000) newVes = izKazan[inp.value.toLowerCase()].ves2000;
        if(ves >= 3000.001 && ves <= 5000) newVes = izKazan[inp.value.toLowerCase()].ves3000;
        if(ves >= 5000.001 && ves <= 10000) newVes = izKazan[inp.value.toLowerCase()].ves5000;
      
        price.textContent = Math.round(newVes * ves) + ' ₽';
      }

      if(ves < 49 && objom < 0.2) {
        price.textContent = izKazan[inp.value.toLowerCase()].minPrice + ' ₽';
      }

      if(objom > 0.2 && objom < 100) {
        let newObjom = 0;
        if(objom <= 0.6 && objom > 0) newObjom = izKazan[inp.value.toLowerCase()].objom06;
        if(objom >= 0.7 && objom <= 3) newObjom = izKazan[inp.value.toLowerCase()].objom3;
        if(objom >= 3.001 && objom <= 5) newObjom = izKazan[inp.value.toLowerCase()].objom5;
        if(objom >= 5.001 && objom <= 12) newObjom = izKazan[inp.value.toLowerCase()].objom12;
        if(objom >= 12.001 && objom <= 20) newObjom = izKazan[inp.valu.toLowerCase()].objom20;
        if(objom >= 20.001 && objom <= 30) newObjom = izKazan[inp.value.toLowerCase()].objom30;
        if(objom >= 30.001 && objom <= 40) newObjom = izKazan[inp.value.toLowerCase()].objom40;
        if(objom >= 40.001 && objom <= 100) newObjom = izKazan[inp.value.toLowerCase()].objom40;

        price.textContent = Math.round(newObjom * objom) + ' ₽';
      }

      dostavka.textContent = izKazan[inp.value.toLowerCase()].dostavkaDay;
      otpravlenie.value = 'Казань';
    }
  }
}

